import { Component, Input, ViewChild, ElementRef } from '@angular/core';


@Component({
    selector: 'mdb-icon',
    template: "<i class=\"fa fa-{{icon}} fa-{{size}} {{class}} prefix\"></i>"
})
export class MdbIconComponent {
    @ViewChild('iconEl') iconEl: ElementRef;
    @Input() icon: string;
    @Input() size: string;
    @Input() class: string;
}
